/*-------------------------------------------------------*/
/* General */
/*-------------------------------------------------------*/
html, body {
  font-family: 'Poppins', sans-serif; }

img {
  max-width: 100%; }

a:focus,
button:focus {
  outline: none;
  box-shadow: none; }

#loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 9999999999999; }

.load-circle {
  margin: 50vh auto;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  text-align: center;
  border-top: 4px solid #000;
  -webkit-animation: rotate 1.6s infinite linear;
  animation: rotate 1.2s infinite linear; }

.load-circle .one {
  width: 50px;
  height: 50px;
  margin-top: 10px;
  border-radius: 50%;
  border-top: 4px solid #000;
  -webkit-animation: rotate 2.2s infinite linear;
  animation: rotate 2.2s infinite linear; }

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

/*-------------------------------------------------------*/
/* section */
/*-------------------------------------------------------*/
.section {
  padding: 100px 0; }

.grey-bg {
  background-color: #f3f3f3; }

.container {
  max-width: 100%;
  width: 1170px; }

@media (max-width: 767px) {
  .section {
    padding: 50px 0; } }

/*-------------------------------------------------------*/
/* Section Title */
/*-------------------------------------------------------*/
.section-title {
  text-align: center;
  padding-bottom: 60px; }

.section-title.left {
  text-align: left; }

.section-title.left h2:after {
  right: auto; }

.section-title.right {
  text-align: right; }

.section-title.right h2:after {
  left: auto; }

.section-title h2 {
  font-weight: 600;
  text-transform: uppercase;
  line-height: 45px;
  font-size: 35px;
  position: relative;
  margin: 0; }

.section-title h2:after {
  width: 60px;
  height: 3px;
  background: #000;
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -10px; }

/*-------------------------------------------------------*/
/* social link */
/*-------------------------------------------------------*/
.social-link ul {
  margin: 0;
  padding: 0; }

.social-link li {
  display: inline-block;
  vertical-align: top;
  margin-right: 5px; }

.social-link li a {
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  border-radius: 50%;
  display: block;
  color: #fff;
  background: #000;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }

/*-------------------------------------------------------*/
/* Buttons */
/*-------------------------------------------------------*/
.m-btn {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  line-height: 50px;
  padding: 0 25px;
  border: none;
  display: inline-block;
  outline: 0;
  border-radius: 100px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }

.m-btn:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0 7px 20px;
  color: #fff;
  text-decoration: none;
  border-radius: 5px; }

.m-btn.m-btn-white {
  background: #fff;
  color: #000; }

/*-------------------------------------------------------*/
/* from color/default.css */
/*-------------------------------------------------------*/
::selection {
  background-color: #f34236;
  color: #fff; }

::-moz-selection {
  background-color: #f34236;
  color: #fff; }

.m-btn,
.service-box .sb-icon,
.header .navbar-nav > li.current > a,
.portfolio-col .hover label,
.call-to-action,
.social-link li a:hover,
.navbar-toggle {
  background-color: #f34236; }

.section-title h2 span,
.fixed-header .logo,
.fixed-header .logo a,
.header .navbar-nav > li > a:hover,
.portfolio-box .filter button.mixitup-control-active,
.portfolio-box .filter button:hover,
.resume-box h4 {
  color: #f34236; }

.contact-us .form-control:focus,
.contact-us textarea.form-control:focus {
  border-color: #f34236; }



