.carusel {
  position: relative;
  overflow: hidden;
  padding: 15px 0 0; }

.carusel ul {
  width: 20000em;
  position: relative;
  /* Optional, required in this case since it's a <ul> element */
  list-style: none;
  margin: 0;
  padding: 0; }

.carusel li {
  /* Required only for block elements like <li>'s */
  float: left; }

.jcarousel-skin-tango .jcarousel-direction-rtl {
  direction: rtl; }

.jcarousel-skin-tango .jcarousel-container-horizontal {
  width: 100%; }

.jcarousel-skin-tango .jcarousel-clip {
  overflow: hidden; }

.jcarousel-skin-tango .jcarousel-clip-horizontal {
  width: 100%;
  height: 75px; }

.jcarousel-skin-tango .jcarousel-item {
  width: 70px;
  height: 70px;
  text-align: center; }

.jcarousel-skin-tango .jcarousel-item-horizontal {
  margin-left: 0;
  margin-right: 10px; }

.jcarousel-skin-tango .jcarousel-direction-rtl .jcarousel-item-horizontal {
  margin-left: 10px;
  margin-right: 0; }

.jcarousel-skin-tango .jcarousel-item-vertical {
  margin-bottom: 10px; }

.jcarousel-skin-tango .jcarousel-item-placeholder {
  background: #fff;
  color: #000; }

/**
 *  Horizontal Buttons
 */
.jcarousel-skin-tango .jcarousel-next-horizontal {
  position: absolute;
  top: -30px;
  right: -1px;
  width: 60px;
  height: 105px;
  cursor: pointer;
  background: transparent url("/tech-icons/arr-carusel-r.png") no-repeat 0 0; }

.jcarousel-skin-tango .jcarousel-direction-rtl .jcarousel-next-horizontal {
  left: -1px;
  right: auto;
  background-image: url("/tech-icons/arr-carusel-r.png"); }

.jcarousel-skin-tango .jcarousel-next-horizontal:hover, .jcarousel-skin-tango .jcarousel-prev-horizontal:hover {
  background-position: 0 -115px; }

.jcarousel-skin-tango .jcarousel-prev-horizontal {
  position: absolute;
  top: -30px;
  left: -1px;
  width: 60px;
  height: 105px;
  cursor: pointer;
  background: transparent url("/tech-icons/arr-carusel-l.png") no-repeat 0 0; }

.jcarousel-skin-tango .jcarousel-direction-rtl .jcarousel-prev-horizontal {
  left: auto;
  right: -1px;
  background-image: url("/tech-icons/arr-carusel-l.png"); }

/* customizations */
.carusel .txt {
  color: #9a9a9a;
  font-size: 12px;
  display: block; }

.carusel .logo-img {
  display: block;
  min-height: 50px; }



