/*-------------------------------------------------------*/
/* Header */
/*-------------------------------------------------------*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }

.header .logo {
  font-size: 25px;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  padding: 6px 0;
  font-weight: 300; }

.header .logo a {
  color: #fff;
  text-decoration: none; }

.header .navbar {
  margin: 0;
  padding: 15px 0;
  border: none;
  border-radius: 0; }

.header .navbar-nav > li {
  padding: 0 5px; }

.header .navbar-nav > li > a {
  color: #fff;
  text-transform: uppercase;
  padding: 10px 15px;
  border-radius: 30px;
  font-weight: 600;
  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }

.header .navbar-nav > li > a:focus,
.header .navbar-nav > li > a:hover {
  background: none; }

.header .navbar-nav > li.current > a:hover {
  color: #fff; }

.fixed-header {
  background: #fff;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08); }

.fixed-header .logo {
  color: #000; }

.fixed-header .navbar-nav > li > a {
  color: #000; }

.fixed-header .navbar-nav > li.current > a,
.fixed-header .navbar-nav > li.current > a:hover {
  color: #fff; }

@media (max-width: 991px) {
  .header .navbar-nav > li {
    padding: 0; }
  .header .navbar-nav > li > a {
    padding: 10px; } }

@media (max-width: 767px) {
  .header .navbar {
    padding: 0; }
  .navbar-collapse.in {
    background: #fff;
    box-shadow: none;
    border: none; }
  .navbar-nav {
    margin-top: 0;
    margin-bottom: 0; }
  .header .navbar-nav > li > a {
    color: #000; }
  .header .navbar-nav > li.current > a {
    color: #fff;
    border-radius: 0; }
  .header .logo {
    margin: 0 0 0 15px;
    padding: 10px 0; }
  .navbar-toggle {
    margin: 7px 15px 3px 0;
    border-radius: 0; }
  .navbar-toggle .icon-bar {
    background: #fff; } }



