/*-------------------------------------------------------*/
/* Home Banner */
/*-------------------------------------------------------*/
.parallax-mirror {
  background: url(home.jpg) #000; }

.home-banner {
  position: relative;
  background-color: #353535; }

video#home-video {
  width: 100%;
  min-height: 300px; }

.home-banner .welcome-text {
  padding: 0;
  text-align: center;
  width: 100%;
  position: absolute;
  display: block;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  transform: translate(0, -50%); }

.home-banner .welcome-text h3 {
  margin: 0;
  /* padding: 0; */
  padding: 20px 0 35px;
  font-size: 55px;
  color: #fff;
  /* font-weight: 300; */
  font-weight: 600;
  letter-spacing: 2px;
  line-height: 70px; }

.home-banner .welcome-text h2 {
  margin: 0;
  padding: 20px 0 35px;
  font-size: 90px;
  color: #fff;
  font-weight: 600;
  letter-spacing: 2px;
  line-height: 80px; }

.home-banner .welcome-text p {
  margin: 0;
  padding: 0 0 50px;
  font-size: 15px;
  color: #fff;
  font-weight: 300;
  letter-spacing: 1px; }

.home-banner .welcome-text .m-btn {
  line-height: 60px;
  padding: 0 45px;
  font-size: 16px;
  letter-spacing: 1px; }

@media (max-width: 767px) {
  .home-banner .welcome-text h3 {
    font-size: 28px; }
  .home-banner .welcome-text h2 {
    padding: 10px 0 15px;
    font-size: 40px;
    line-height: 40px; }
  .home-banner .welcome-text p {
    padding: 0 0 15px;
    font-size: 14px; }
  .home-banner .welcome-text .m-btn {
    line-height: 40px;
    padding: 0 25px;
    font-size: 13px; } }



