/*-------------------------------------------------------*/
/* Contact */
/*-------------------------------------------------------*/
.contact-form {
  background: #fff;
  border-radius: 10px;
  box-shadow: rgba(58, 78, 95, 0.2) 0 10px 16px, rgba(58, 78, 95, 0.05) 0 -5px 16px;
  padding: 20px; }

.contact-us .form-group {
  margin-bottom: 35px; }

.contact-us .form-group.contact-message {
  margin-bottom: 25px; }

.contact-us .contact-feedback.success {
  background: #fff none repeat scroll 0 0;
  color: #50B948;
  font-weight: 700;
  padding: 20px;
  text-align: center; }

.contact-us .contact-feedback.error {
  background: #fff none repeat scroll 0 0;
  color: #ff0000;
  font-weight: 700;
  padding: 20px;
  text-align: center; }

.contact-us .form-control {
  border: none;
  border-bottom: 2px solid #a2a2a2;
  box-shadow: none;
  padding: 6px 0;
  border-radius: 0; }

.contact-us .form-group.action {
  margin-bottom: 0;
  text-align: center; }

.contact-us textarea.form-control {
  border: 2px solid #a2a2a2;
  padding: 6px;
  margin-bottom: 6px; }

.contact-info {
  position: relative;
  padding: 10px 10px 10px 90px;
  margin-bottom: 30px;
  box-shadow: rgba(58, 78, 95, 0.2) 0 10px 16px, rgba(58, 78, 95, 0.05) 0 -5px 16px;
  border-radius: 10px; }

.contact-info i {
  font-size: 16px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  position: absolute;
  left: 20px;
  top: 10px;
  background: #000;
  border-radius: 50%;
  color: #fff; }

.contact-info a {
  color: #666; }

.contact-info h4 {
  margin: 0 0 10px;
  font-weight: 600; }

.contact-info p {
  font-size: 15px;
  color: #666;
  margin: 0; }

@media (max-width: 767px) {
  .contact-form {
    margin-bottom: 30px; } }



