/*-------------------------------------------------------*/
/* Resume Box */
/*-------------------------------------------------------*/
.resume-box {
  background: #fff;
  border-radius: 10px;
  padding: 30px 20px;
  box-shadow: rgba(58, 78, 95, 0.2) 0 10px 16px, rgba(58, 78, 95, 0.05) 0 -5px 16px;
  width: 100%;
  float: left;
  margin-bottom: 15px; }

.resume-box h4 {
  font-size: 22px;
  margin: 0;
  padding: 0 0 20px;
  font-weight: 600; }

.resume-box .resume-row {
  margin-bottom: 30px; }

.resume-box h5 {
  margin: 0;
  padding: 0 0 10px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 16px; }

.resume-box label {
  font-weight: normal;
  font-style: italic;
  font-size: 14px; }

.resume-box p {
  margin: 0;
  font-size: 15px; }

@media (max-width: 767px) {
  .resume-box {
    margin: 15px 0; } }



