/*-------------------------------------------------------*/
/* Portfolio */
/*-------------------------------------------------------*/
.portfolio-box .filter {
  text-align: center;
  padding-bottom: 60px; }

.portfolio-box .filter button {
  padding: 0;
  border: none;
  text-transform: uppercase;
  color: #000;
  background: none;
  font-size: 16px;
  margin: 0 10px; }

.portfolio-col {
  position: relative;
  margin-bottom: 30px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: rgba(58, 78, 95, 0.2) 0 10px 16px, rgba(58, 78, 95, 0.05) 0 -5px 16px;
  overflow: hidden; }

.portfolio-col .hover {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  padding: 0;
  color: #fff;
  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }

.portfolio-col .hover h4 {
  margin: 0;
  padding: 0 0 10px;
  font-size: 20px;
  font-weight: 600; }

.portfolio-col .hover h5 {
  position: absolute;
  bottom: 10px; }

.portfolio-col .hover label {
  margin: 0 4px 4px 0;
  padding: 0 0 15px;
  font-size: 12px;
  font-weight: 300;
  padding: 5px 20px;
  border-radius: 30px; }

.portfolio-col:hover .hover {
  opacity: 1;
  padding: 20px;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px; }

@media (max-width: 767px) {
  .portfolio-box .filter {
    padding-bottom: 30px; }
  .portfolio-box .filter button {
    margin: 0 5px; }
  .portfolio-col:hover .hover {
    padding: 10px; }
  .portfolio-col .hover h4 {
    font-size: 16px; } }



