/* ABOUT ME*/
/*-------------------------------------------------------*/
.personal-pic {
  position: relative;
  border-radius: 10px;
  padding: 10px;
  box-shadow: rgba(58, 78, 95, 0.2) 0 10px 16px, rgba(58, 78, 95, 0.05) 0 -5px 16px;
  overflow: hidden; }

.personal-pic img {
  display: block;
  margin: 0 auto; }

.personal-info .section-title {
  padding-bottom: 10px; }

.personal-info h2 {
  margin-top: 0px; }

.personal-info .social-link {
  padding: 10px 0 35px; }

.personal-info .m-btn {
  line-height: 40px;
  padding: 0 40px;
  margin-right: 10px; }

@media (max-width: 767px) {
  .personal-info {
    padding-top: 40px; }
  .personal-info .m-btn {
    padding: 0 15px; } }



