/* Footer */
/*-------------------------------------------------------*/
.footer {
  background: #121212; }

.footer .footer-logo {
  padding: 0 0 20px;
  margin: 0;
  color: #fff;
  font-size: 22px;
  letter-spacing: 2px;
  font-weight: 300; }

.footer .main-footer {
  padding: 40px 0; }

.footer .sub-footer {
  background: #171717;
  color: #999;
  padding: 25px 0; }

.footer .sub-footer p {
  margin: 0; }



